<template>
	<div class="page-case">
		<v-header curPge="case"></v-header>
		<div class="about">
			<div class="about-wrapper">
				<div class="about-us">产品案例</div>
				<div class="about-desc">产品&解决方案</div>
			</div>
		</div>
		<div class="case-list">
			<div class="case-wrapper">
				<ul class="flex pdt-24 pdb-24 text-left">
					<li>分类：</li>
					<li :class="{'item-active':type=='all'}" @click="filterType('all')">全部</li>
					<li :class="{'item-active':type=='qkl'}" @click="filterType('qkl')">区块链</li>
					<li :class="{'item-active':type=='wlw'}" @click="filterType('wlw')">物联网</li>
				</ul>
				<div class="case-item-wrapper flex-and-wrap">
					<div class="case-item" @click="detail('/detail/1','九九环保智能平台')"  v-if="type=='wlw'">
						<a href="javascript:;"><img width="100%" src="../../assets/img/icons/99.png" lazy="loading"></a>
						<div class="case-name">九九环保智能平台</div>
					</div>
					<div class="case-item" @click="detail('/detail/2','新奥燃气管网监测系统可视化平台')"  v-if="type=='wlw'">
						<a href="javascript:;"><img width="100%" src="../../assets/img/icons/xa.png" lazy="loading"></a>
						<div class="case-name">新奥燃气管网监测系统可视化平台</div>
					</div>
					<div class="case-item" @click="detail('/detail/3','欧特云智能巡检平台')"  v-if="type=='wlw'">
						<a href="javascript:;"><img width="100%" src="../../assets/img/icons/wlw3.png" lazy="loading"></a>
						<div class="case-name">欧特云智能巡检平台</div>
					</div>

					<div class="case-item" @click="detail('/detail/4','雅典娜法律咨询平台')" v-if="type=='all'">
						<a href="javascript:;"><img width="100%" src="../../assets/img/icons/all1.png" lazy="loading"></a>
						<div class="case-name">雅典娜法律咨询平台</div>
					</div>
					<div class="case-item" @click="detail('/detail/5','塔娜交易所')" v-if="type=='qkl'">
						<a href="javascript:;"><img width="100%" src="../../assets/img/icons/qkl1.png" lazy="loading"></a>
						<div class="case-name">塔娜交易所</div>
					</div>
				</div>
			</div>
		</div>
		<v-footer />
	</div>	
</template>
<script>
	import Vue from 'vue'
	import VueLazyload from 'vue-lazyload'
	import Header from "@/components/header.vue"
	import Footer from "@/components/footer.vue"
	Vue.use(VueLazyload)
	export default{
		name:"case",
		components:{
      		"v-header":Header,
      		"v-footer":Footer
      	},
		data(){
			return {
				dafaultImg:require("../../assets/img/temp/loading.gif"),
				type:"all"
			}
		},
		mounted(){
			$('.about-us').textillate({ in: { effect: 'fadeInRight'} });
			$('.about-desc').textillate({ in: { effect: 'fadeInRight'} });
		},
		methods:{
			detail(path,caseName){
				this.$router.push(path);
				sessionStorage.caseName = caseName;
			},
			filterType(type){
				this.type = type;
			}
		}
	}
</script>
<style lang="scss" scoped>
	@import '../../assets/scss/mixin';
	.page-case{
		.about{
			width: 100%;
			height: px2vw(320);
			background: url("../../assets/img/icons/about.png") no-repeat;
			background-size: 100% 100%;

			.about-wrapper{
				width: 100%;
				height: 100%;
				background: rgba(0,12,63,0.7);
				padding-left: px2vw(370);
				text-align: left;
				padding-top: px2vw(84);
				color:rgba(255,255,255,1);
			}
			.about-us{
				font-size:px2vw(40);
				font-weight:bold;
			}
			.about-desc{
				font-size:px2vw(18);
				font-weight:400;
				margin-top: px2vw(16);
			}
		}
		.case-list{
			width: 100%;
			background: #EFF3F8;
			.case-wrapper{
				width: px2vw(1200);
				margin: 0 auto;
				li{
					margin-right: px2vw(60);
					font-size: px2vw(14);
					cursor: pointer;

					&:hover{
						color:#1ccdff;
					}
				}
			}
			.case-item{
				width: px2vw(280);
				height: px2vw(354);
				background: #3598DB;
				background-size: 100% 100%;
				margin-right: px2vw(19);
				margin-bottom: px2vw(20);
				position: relative;
				box-shadow: 4px 4px 5px rgba(0,0,0,0.2);
				overflow: hidden;
  				img{
  					transition: transform 0.75s;
  				}
				img:hover{
					transform: scale(1.1);
				}
				a{
					height: calc(100% - 70px);
					overflow: hidden;
				}
				a:hover {
				    text-decoration: none;
				    color: #c89a69;
				}
			}
			.case-name{
				position: absolute;
				width: 100%;
				height: px2vw(70);
				line-height: px2vw(70);
				bottom: 0;
				left: 0;
				overflow: hidden;
				background: #fff;
				padding-left: px2vw(12);
				font-size: px2vw(16);
			}
		}
		.item-active{
			color: #1ccdff;
		}
		// img[lazy=loading]{
		// 	background: url("../../assets/img/temp/loading.gif") center center no-repeat;
		// 	background-size: 100% 100%;
		// }
		.toUp{
			animation-duration: 1s;
			animation-name: toUp;
			animation-delay: 0.2s;
		}
		@keyframes toUp{
			0%{
				opacity: 0;
				transform: translateY(20px);
			}
			100%{
				opacity: 1;
				transform: translateY(0);
			}
		}
		.case-item-wrapper{
			min-height: 500px;
		}
	}
</style>


